<template>
  <div class="tab-bar-item" @click="itemClick">
    <div v-if="!isActive"><slot name="item-icon"></slot></div>
    <div v-else><slot name="item-active"></slot></div>
    <div :style="activeStyle"><slot name="item-name"></slot></div>
  </div>
</template>

<script>
export default {
  name: "TabBarItem", // 要有引号
  data(){
    return{
      // isActive: true,
    }
  },
  props:{
    path: String,
    activeColor: {
      type: String,
      default: "red"
    }
  },
  computed:{
    isActive(){
      return this.$route.path.indexOf(this.path) !== -1;
    },
    activeStyle(){
      return this.isActive? {color: this.activeColor} :{}
    }
  },
  methods: {
    itemClick(){
      this.$router.push(this.path);
    }
  }
}
</script>

<style>
  .tab-bar-item{
    /* 设置间距相等 */
    flex: 1; 
    /* 设置居中对齐 */
    text-align: center;
    bottom: 0;
    font-size: 14px;
  }
  .tab-bar-item img{
    height: 24px;
    vertical-align: middle;
    margin-top: 3px;
  }
  .tab-bar-item p{
    margin: 0;
  }

</style>