<template>
  <div>
      <nav-bar class="home-nav">
        <div slot="center">购物街</div>
      </nav-bar>
      <div class="main-body">
        <mainSwiper :banners="banners"></mainSwiper>
        <home-rec :recommends="recommends"></home-rec>
        <home-feature></home-feature>
        <tab-control :titles="['流行','新款','精选']"></tab-control>
      </div>
  </div>
</template>

<script>
import mainSwiper from "./childComponents/HomeSwiper"
import homeRec from "./childComponents/HomeRec"
import homeFeature from "./childComponents/HomeFeature"

import navBar from "components/common/navbar/NavBar"
import tabControl from "components/content/tabControl/TabControl"

import {getHomeMultiData} from "network/home"

export default {
  name: "Home",

  components:{
    mainSwiper,
    homeRec,
    homeFeature,

    navBar,
    tabControl,
  },

  data(){
    return{
      banners : null,
      recommends : null,
    }
  },

  created(){
    getHomeMultiData()
    .then(res=>{
      console.log(res);
      this.banners = res.data.banner.list;
      this.recommends = res.data.recommend.list;
    })
  }
}
</script>

<style>
  .home-nav{
    background-color: var(--color-tint);
    color: white;
  }
  .main-body{
    margin-top: 44px;
    margin-bottom: 49px;
  }
  
</style>